	module.exports = {"data":{"markdownRemark":{"html":"<p>The decorator is a design pattern that could modify or enhance the interface of a class.</p>\n<h2>Decorator in Python3</h2>\n<p>Here is an example of Decorator implementation in Python3.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-python\"><code><span class=\"token keyword\">class</span> <span class=\"token class-name\">MyText</span><span class=\"token punctuation\">(</span>object<span class=\"token punctuation\">)</span><span class=\"token punctuation\">:</span>\n    <span class=\"token keyword\">def</span> <span class=\"token function\">__init__</span><span class=\"token punctuation\">(</span>self<span class=\"token punctuation\">,</span> text<span class=\"token operator\">=</span><span class=\"token string\">''</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">:</span>\n        self<span class=\"token punctuation\">.</span>text <span class=\"token operator\">=</span> text\n\n    <span class=\"token keyword\">def</span> <span class=\"token function\">__str__</span><span class=\"token punctuation\">(</span>self<span class=\"token punctuation\">)</span><span class=\"token punctuation\">:</span>\n        <span class=\"token keyword\">return</span> <span class=\"token string\">'This is {}'</span><span class=\"token punctuation\">.</span>format<span class=\"token punctuation\">(</span>self<span class=\"token punctuation\">.</span>text<span class=\"token punctuation\">)</span>\n\n\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">BracketDecorator</span><span class=\"token punctuation\">(</span>object<span class=\"token punctuation\">)</span><span class=\"token punctuation\">:</span>\n    <span class=\"token keyword\">def</span> <span class=\"token function\">__init__</span><span class=\"token punctuation\">(</span>self<span class=\"token punctuation\">,</span> decoratee<span class=\"token punctuation\">)</span><span class=\"token punctuation\">:</span>\n        self<span class=\"token punctuation\">.</span>_decoratee <span class=\"token operator\">=</span> decoratee\n\n    <span class=\"token keyword\">def</span> <span class=\"token function\">__str__</span><span class=\"token punctuation\">(</span>self<span class=\"token punctuation\">)</span><span class=\"token punctuation\">:</span>\n        <span class=\"token keyword\">return</span> <span class=\"token string\">'({})'</span><span class=\"token punctuation\">.</span>format<span class=\"token punctuation\">(</span>self<span class=\"token punctuation\">.</span>_decoratee<span class=\"token punctuation\">)</span>\n\n\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">QuoteDecorator</span><span class=\"token punctuation\">(</span>object<span class=\"token punctuation\">)</span><span class=\"token punctuation\">:</span>\n    <span class=\"token keyword\">def</span> <span class=\"token function\">__init__</span><span class=\"token punctuation\">(</span>self<span class=\"token punctuation\">,</span> decoratee<span class=\"token punctuation\">)</span><span class=\"token punctuation\">:</span>\n        self<span class=\"token punctuation\">.</span>_decoratee <span class=\"token operator\">=</span> decoratee\n\n    <span class=\"token keyword\">def</span> <span class=\"token function\">__str__</span><span class=\"token punctuation\">(</span>self<span class=\"token punctuation\">)</span><span class=\"token punctuation\">:</span>\n        <span class=\"token keyword\">return</span> <span class=\"token string\">'\\\"{}\\\"'</span><span class=\"token punctuation\">.</span>format<span class=\"token punctuation\">(</span>self<span class=\"token punctuation\">.</span>_decoratee<span class=\"token punctuation\">)</span>\n\n\n<span class=\"token keyword\">print</span><span class=\"token punctuation\">(</span>BracketDecorator<span class=\"token punctuation\">(</span>MyText<span class=\"token punctuation\">(</span><span class=\"token string\">'apple'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n<span class=\"token operator\">>></span><span class=\"token operator\">></span> <span class=\"token punctuation\">(</span>This <span class=\"token keyword\">is</span> apple<span class=\"token punctuation\">)</span>\n<span class=\"token keyword\">print</span><span class=\"token punctuation\">(</span>QuoteDecorator<span class=\"token punctuation\">(</span>MyText<span class=\"token punctuation\">(</span><span class=\"token string\">'banana'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n<span class=\"token operator\">>></span><span class=\"token operator\">></span> <span class=\"token string\">\"This is banana\"</span>\n</code></pre>\n      </div>","fields":{"slug":"/software-engineering/design-patterns/decorator/"},"frontmatter":{"title":"Decorator"}}},"pathContext":{"slug":"/software-engineering/design-patterns/decorator/"}}

